<template>
  <div class="special">
  	<Unit></Unit>
  	<!-- <Download></Download> -->
  	<!-- <h2>
  		<span class="arrowLeft" onclick="window.history.go(-1)"></span>
  		<p>{{session_detail.title}}</p>
  	</h2> -->
	<div class="special_content">
		<!-- 有图	 -->
		<div v-if="session_detail.banner" class="special_hasImg">
			<img v-lazy="imgUrl+session_detail.banner+'@!1200'" alt="">
			<p>{{session_detail.title}}</p>
			<p><span><em >{{session_detail.minDiscountPercent|capitalize0}}</em>折起</span><i>每满{{session_detail.discountThresholdCent|transMoney}}再减{{session_detail.discountCent|transMoney}}上不封顶</i></p>
		</div>
		<!-- 无图 -->
		<dl v-else-if="session_detail.brandLogo" class="special_hasnotImg" >
			<dt>
				<img  v-bind:src="imgUrl+session_detail.brandLogo+'@!800'" alt="">
			</dt>
			<dd>
				<p>{{session_detail.title}}</p>
				<p><span>{{session_detail.minDiscountPercent|capitalize0}}</span>折起</p>
				<p>每满{{session_detail.discountThresholdCent|capitalize}}再减{{session_detail.discountCent|capitalize}}上不封顶</p>
			</dd>
		</dl>
		<h3 ref='countDown'>
			距离结束<span>{{time|day}}</span>天<span>{{time|hour}}</span>时<span>{{time|min}}</span>分<span>{{time|second}}</span>秒
		</h3>
		<GoodsList :GoodsData='GoodsData'></GoodsList>
	</div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
import Unit from './common_components/Unit'
export default {
  name: 'Special',
  data () {
    return {
      session_detail: {},
      time:0,
      GoodsData:[],
      specialPage:1,
      specialBool:1,
      specialFlag:true,
      sessionId:undefined,
      imgUrl:''
    }
  },
  created(){
  	this.imgUrl = this.imgUrlPrefix ? this.imgUrlPrefix : localStorage.imgUrlPrefix;
  	this.sessionId = this.$route.query.sessionId;
  	console.log(this.sessionId)
  	this.getSession_detail(this.sessionId);
  	this.getSpecial_list(this.sessionId,this.specialPage)
  },
  computed:{
      imgUrlPrefix () {
        return this.$store.state.imgUrlPrefix
        console.log(this.$store.state.imgUrlPrefix)
      }
  },
  methods:{
  	getSession_detail(id){
  		this.$http({
  			method:'get',
  			url:this.$api+'/h5/sales/session_detail',
  			params:{
  				id:id
  			}
  		}).then(res=>{
  		     this.$set(this,'session_detail',res.data.value)
  		     // this.session_detail=res.data.value;
  			console.log(res.data.value)
  			//兼容safari浏览器
  			let endTime  = res.data.value.endTime.replace(/-/g,'/')
  			// console.log(endTime)
  			this.time = (new Date(endTime)).getTime()-(new Date()).getTime();
  			this.countDown(this.$refs.countDown);
  		}).catch(error=>{
  				let instance = Toast('网络好像有问题,请稍候再试试');
				setTimeout(() => {
				  instance.close();
				}, 2000);
  			})
  	},
  	getSpecial_list(id,page){
  		if(this.specialPage == this.specialBool){
  			this.specialBool = false;
  			this.$http({
  				method:'get',
  				url:this.$api+'/h5/sales/session_item_list',
  				params:{
  					sessionId:id,
  					currentPage:page,
  					pageSize:8
  				}
  			}).then(res=>{
  				if(res.data.value.values.length){
  					res.data.value.values.forEach((item)=>{
  						this.GoodsData.push(item);
  					})
  					this.specialPage++;
  					this.specialBool = this.specialPage;
  				}else{
  					this.specialFlag = false;
  				}
  			}).catch(error=>{
  				let instance = Toast('网络好像有问题,请稍候再试试');
				setTimeout(() => {
				  instance.close();
				}, 2000);
  			})
  		}
  	},
  	nextPage(){
  		window.onload = window.onscroll = ()=>{
	        let top = document.documentElement.scrollTop || document.body.scrollTop;
	        //文本高度
	        let textHeight = document.body.scrollHeight;
	        //屏幕高度
	        let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
	        //热卖商品高度
	        if(textHeight-top-clientHeight<100 && this.specialFlag){
	          this.getSpecial_list(this.sessionId,this.specialPage)
	        }
        }
  	},
  	countDown(dom){
		setInterval(()=>{ 
			// console.log(this.time)
			this.time -= 1000;
			let second = parseInt(this.time/1000)%60,
				min    = (parseInt(this.time/1000)-second)/60%60,
				hour   = ((parseInt(this.time/1000)-second)/60-min)/60%24,
				day    = (((parseInt(this.time/1000)-second)/60-min)/60-hour)/24;
				dom.innerHTML='距离结束<span>'+(day>0?day:0)+'</span>天<span>'+(hour<10?'0'+hour:hour)+'</span>时<span>'+(min<10?'0'+min:min)+'</span>分<span>'+(second<10?'0'+second:second)+'</span>秒';
				for(let i=0;i<dom.children.length;i++){	
					dom.children[i].style.cssText = 'display: inline-block;background:#ffffff;width:20px;height:20px;line-height: 20px;margin: 0 3px;color:#333234'
				}
		},1000)

  	}
  },
  filters: {
	  capitalize00: function (value) {
	   	if (!value) return '0'
  		return parseFloat(value/100).toFixed(2)
	  },
	  capitalize0: function (value) {
	   	if (!value) return '0'
  		return parseFloat(value/10).toFixed(1)
	  },
	  capitalize: function (value) {
	   	if (!value) return '0'
  		return parseInt(value/100)
	  },
	  day(value){
	  	 let second = parseInt(value/1000)%60,
		     min    = (parseInt(value/1000)-second)/60%60,
		     hour   = ((parseInt(value/1000)-second)/60-min)/60%24,
		     day    = (((parseInt(value/1000)-second)/60-min)/60-hour)/24;
		  return day;
	  },
	  hour(value){
	  	 let second = parseInt(value/1000)%60,
		     min    = (parseInt(value/1000)-second)/60%60,
		     hour   = ((parseInt(value/1000)-second)/60-min)/60%24;
		  return hour<10?'0'+hour:hour;
	  },
	  min(value){
	  		let second = parseInt(value/1000)%60,
		     min    = (parseInt(value/1000)-second)/60%60;
		  return min<10?'0'+min:min;
	  },
	  second(value){
	  	let  second = parseInt(value/1000)%60;
		  return second<10?'0'+second:second;
	  }
	},
  mounted(){
    this.nextPage();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
@import '../common/css/var.scss';
h2{
	display: flex;
	position: fixed;
	top: 44px;
	width: 100%;
	height: 44px;
	background: white;
	align-items:center;
	padding: 0 15px;
	box-sizing:border-box;
	border:1px solid #f4f4f4;/*no*/
	background: white;
	z-index: 999;
	.arrowLeft{
		background: url('../assets/order_navbar_arrow_back_icon@3x.png');
		display: inline-block;
		width: 24px;
		height: 24px;
		background-size:100%; 
	}
	p{
		width: 300px;
		font-size:18px;
		color:#212121;
		letter-spacing:0;
		line-height:18px;
		text-align:center;
		position:absolute;
		left: 50%;
		margin-left: -150px;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
		line-height: 44px;
	}
}
.special_content {
	background: $backgroundColor;
	
	text-align: center;
	max-width: 414px;
	/*margin: 88px auto 0;*/


	.special_hasImg{
		margin: 0 auto;
		background: white;
		width: 355px;
		box-sizing:border-box;
		padding: 10px;
		box-shadow:0px 5px 15px rgba(204,205,203,0.15);
		
		img{
			width: 100%;
			height: 201px;
		}
		p:nth-child(2){
			margin: 15px 0;
			font-family:PingFangSC-Medium;
			font-size:18px;
			color:#333234;
			overflow: hidden;
			text-overflow:ellipsis;
			white-space: nowrap;
		}
		p:nth-child(3){
			span{
				font-size:14px;
				color:#ff3766;
				line-height:18px;
				margin-right: 10px;
				em{
					font-size:18px;
				}

			}
			i{
				font-size:12px;
				color:#333234;
			}
		}
	}
	.special_hasnotImg{
		width: 335px;
		margin: 0 auto;
		dt{
			width: 80px;
			height: 80px;
			overflow: hidden;
			border-radius: 50%;
			background: white;
			overflow: hidden;
			margin: 0 auto;
			border:1px solid #F4F4F4;/*no*/
			position:relative;
			img{
				width: 100%;
			}
		}
		dd{
			width: 335px;
			height: 185px;
			background: white;
			margin-top: -40px;
			box-sizing:border-box;
			padding-top: 50px;
			box-shadow:0px 5px 15px rgba(204,205,203,0.15);
			p:nth-child(1){
				font-family:PingFangSC-Semibold;
				font-size:18px;
				color:#333234;
				line-height:18px;
				text-align:center;
			}
			p:nth-child(2){
				margin: 25px auto 11px;
				background:#ff3766;
				width:80px;
				height:34px;
				color: white;
				line-height: 34px;
				span{
					font-size:20px;
				}
			}
			p:nth-child(3){
				font-size:14px;
				color:#333234;
			}
		}
	}
}
h3{
	height: 40px;
	line-height: 40px;
	margin: 10px 0 5px;
	font-size:12px;
	color:#B2B2B2;
	display: flex;
	justify-content:center;
	align-items:center;
	span{
		display: inline-block;
		background:#ffffff;
		width:20px;
		height:20px;
		line-height: 20px;
		margin: 0 3px;
		color: #333234;
	}
}
</style>



